.content {
  margin-top: 120px;
  overflow-y: auto;
  margin-bottom:40px;
  justify-items: self-end;
}
.fixed-slide{
  overflow: hidden;
  background: white;
  position: fixed;
  top: 60px;
  width: 100%;
}
.video-footer{
  display: flex;
  max-width:360px;

}
.profile-pic > img{
  width :40px;
  padding:10px 5px;
  border-radius: 50%;
  flex : 1 1;
}
.video-detail{
  padding:10px;
  display: flex;
  flex-direction: column;
  max-width:300px;
  flex: 1 1;
  justify-content: space-around;
  align-self: center;

}
.title{
  font-size: 14px;
  overflow: hidden;
  height:18px;

}
.channel{
  font-size: 12px;
  

}
.link{
  text-decoration: none;
  color:black;
}
@media (min-width : 420px){
  .section{
    display:flex;
  }
.content{
  display:flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

.video{
  margin:24px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.12);
  background: #fbad1a;
  
}
iframe{
  width:240px;
  height: 100%
}
}
