.footer{
  position :fixed;
  width:100vw;
  background-color:rgb(250, 250, 250);
  bottom:0;
  box-shadow: 1px 1px 4px 0.5px grey;
  display:flex;
  height:50px;
  align-items : center;
  justify-content: space-around
  
}
.footer span{
  position: absolute;
  bottom:0;
  left:50%;
}
@media (min-width : 420px){

  .footer{
    display: none;
  }
}