:root {
  --primary_color: #3c3c44;
  --secondary_colory: #fbad1a;
  --dark: #3c3c44;
}

.logo {
  display: flex;
  align-items: center;
  padding: 10px;
}
.logo .logo-image {
  width: 40px;
  padding-right: 5px;
}
.title {
  color: var(--primary_color);
}
.title span {
  color: var(--secondary_colory);
}
.search-icon {
  width: 20px;
  position: absolute;
  right: 45px;
}
.user-icon {
  width: 20px;
  position: absolute;
  right: 20px;
}

.header-wrapper {
  display: flex;
  min-height: 60px;
  width: 100vw;
}
.logo input {
  display: none;
}

@media (min-width: 420px) {
  .logo {
    display: flex;
    font-size: 20px;
    align-items: center;
  }
  .logo .logo-image {
    width: 60px;
  }

  .search {
    width: 30px;
    position: absolute;
    right: 10px;
  }
}
