.library{
  margin-top: 60px;
  overflow-y: auto;
  margin-bottom:40px;
  justify-items: self-end;
  
}
.library-container{
  display: flex;
  flex-direction: column;
  box-shadow: var(--shadow);
  padding :10px;
  font-size: 18px;

}
.link{
  text-decoration: none;
  color:black;
}
.library-item{
  display :flex;
  padding:10px;
}
.library-item > img{
  width: 30px;
  margin-right: 10px;

}
.library-item > span{
  align-self: auto;
  
}