.slider-wrapper::-webkit-scrollbar {
  width: 0;
}
.slider-wrapper {
  display: flex;
  background: #fff;
  overflow-x: auto;
}

.slider-wrapper .slider-item {
  font-size: 12px;
  background: #eeeef1;
  color:#3c3c44;
  padding: 7px 15px;
  border-radius: 12px;
  text-align: center;
  margin: 0.4rem;
}

@media (min-width: 420px) {
  .slider-wrapper {
    justify-content: center;
    align-self: center;
  }
}
